<template>
  <div class="row justify-content-center mt-5">
    <div class="col-lg-5 col-md-12 col-sm-12 col-12">
      <div class="jumbotron bg-white pt-5">
        <form @submit.prevent="submitHandler">
          <h3 class="text-center">Логин</h3>
          <div class="form-group">
            <label for="email">Email</label>
            <input
              id="email"
              v-model="email"
              type="email"
              class="form-control"
              required
            />
          </div>
          <div class="form-group">
            <label for="password">Пароль</label>
            <input
              id="password"
              v-model="password"
              type="password"
              class="form-control"
              required
            />
          </div>
          <button type="submit" class="btn btn-warning w-100 mt-3">
            Войти
          </button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: () => ({
    email: '',
    password: '',
  }),
  methods: {
    async submitHandler() {
      const formData = {
        email: this.email,
        password: this.password,
      }
      // eslint-disable-next-line no-useless-catch
      try {
        await this.$store.dispatch('login', formData)
        this.$router.push('/catalog?message=logged')
        // eslint-disable-next-line no-empty
      } catch (e) {}
    },
  },
}
</script>

